.main {
  padding: 20px 60px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  h1 {
    color: #111747;
  }
  p {
    color: gray;
  }
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
    figure {
      width: 300px;
      height: 400px;
      cursor: pointer;
      :hover {
        transform: scale(1.03);
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
          rgba(0, 0, 0, 0.23) 0px 6px 6px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__spin {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
